body{
	margin: 0;
	color: #000;
	font: 16px/18px Ubuntu, Arial, Helvetica, sans-serif;
	min-width: 320px;
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: none;
	background-color: #fff;
}
img{
	border-style: none;
	vertical-align: top;
}
a:active{
	background-color: transparent;
}
input,
textarea,
select{
	font: 100% Arial, Helvetica, sans-serif;
	vertical-align: middle;
	color: #000;
}
form,fieldset{
	margin: 0;
	padding: 0;
	border-style: none;
}
header,
footer,
article,
section,
nav,
figure,
aside{
	display: block;
}
figure{
	margin: 0;
	padding: 0;
}
.nav{
	overflow: hidden;
	list-style-type: none;
	margin: 0;
	padding: 0;
}
.nav-frame{
	float: left;
}
.nav-btn{
	display: block;
	padding: 10px;
	border: 1px solid #000;
	color: #000;
	text-align: center;
	outline: none;
}
.nav-btn.speed{
	display: inline-block;
	width: 50px;
}
.nav-btn.speed:hover{
	cursor: default;
	background: #fff;
	color: #000;
}
.nav-btn:hover{
	cursor: pointer;
	color: #fff;
	background: #555;
}
.speed-btn{
	display: inline-block;
	margin: 0 0 0 -4px;
}
.map{
	display: inline-block;
	list-style-type: none;
	margin: 0;
	padding: 0;
	border: 1px solid #eee;
	border-width: 1px 0 0 1px ;
}
.map-frame{
	position: relative;
	display: inline-block;
	vertical-align: top;
	width: 50px;
	height: 50px;
	border: 1px solid #eee;
	border-width: 0 1px 1px 0;
}
.map-frame:hover{
	cursor: pointer;
}
.energy{
	position: absolute;
	right: 0;
	bottom: 0;
	width: 4px;
	height: 0;
	background: url(../images/energy-half.jpg) 0 100% no-repeat;
}
.energy.e1{ height: 10px;}
.energy.e2{ height: 20px;}
.energy.e3{ height: 30px;}
.energy.e4{ height: 40px;}
.energy.e5{ height: 50px;}
.stone{
	background: url(../images/stone.jpg) no-repeat;
	background-size: cover;
}
.cow{
	background: url(../images/cow.jpg) 2px 50% no-repeat;
	background-size: 74%;
}
.grass{
	background: url(../images/grass.jpg) 2px 50% no-repeat;
	background-size: 74%;
}
.cell-nav{
	display: none;
	list-style-type: none;
	margin: 0;
	padding: 0;
	position: absolute;
	z-index: 9;
	top: 0;
	left: 50px;
	width: 200px;
	background: #fff;
	border: 1px solid #555;
}
.map .cell-nav{ display: block;}
.cell-nav-frame{
	display: none;
}
.map-frame.empty .cell-nav-frame.cell-nav-empty,
.map-frame.stone .cell-nav-frame.cell-nav-stone,
.map-frame.cow .cell-nav-frame.cell-nav-object,
.map-frame.grass .cell-nav-frame.cell-nav-object{ display: block;}
.cell-nav-btn{
	display: block;
	box-sizing: border-box;
	width: 100%;
	border: 1px solid #ccc;
	text-align: left;
	background: #fff;
}
.cell-nav-btn:hover{
	cursor: pointer;
	background: #eee;
}